<template>
  <div> 
    <div class="contentCellBox">
      <div class="contentCellBox">
       <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td>序</td>
                  <td v-for="(item,index) in tableHeader">{{item.title}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td v-for="(item2,index2) in tableHeader">
                      {{item[item2.key]}}
                    </td>
                   </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
   </div>
</template>
<script>
export default {
  components: {
  },
  props:['dataObj'],
  created() {
    this.axios
        .get("/forecast/browseData/"+this.dataObj.id)
        .then(response => {
           if (response.data) {
                this.tableBody = response.data.datas;
                this.tableHeader = response.data.columns;
            }
        })
        .catch(error => {});
  },
  data() {
    return {
      tableHeader: [],
      tableBody: [{}]
    };
  },
  mounted() {
  },
  watch: {
    dataObj(val){
      this.axios
        .get("/forecast/browseData/"+val.id)
        .then(response => {
            if (response.data) {
              this.tableBody = response.data.datas;
              this.tableHeader = response.data.columns;
            }
        })
        .catch(error => {});    
    }
  },
  methods:{
    cancel() {},
  }
};
</script>


