<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">机器学习</span>
      <span class="topTitle-span1">
        <img src="../../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">预测数据集</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{index+1}}</td>
                  <td>{{item.forecastDatasource}}</td>
                  <td>{{item.forecastTable}}</td>                  
                  <td>{{item.forecastDesc}}</td>
                  <td>
                    <span class="spanBox"  @click="browseData(item.id)">数据浏览</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </Row>
       </div>
        <Modal :mask-closable="false"   v-model="showModal" :title="modelTitle" @on-cancel="cancel" class="dialog" :styles="{width: '80%'}">
          <component :is="tabview" @cancel="cancelDialog" :dataObj="dataObj" width="80%"></component>
          <div slot="footer"></div>
        </Modal>  
      </div>
    </div>
  </div>
</template>
<script>
import browseData from "@/components/modelManage/machineLearnManage/dataMangage/browseData2";
export default {
  components: {
    browseData
  },
  created() {
    this.axios
        .get("/forecast/list")
        .then(response => {
            if (response.data) {
                this.tableBody = response.data;
            }
        })
        .catch(error => {});
  },
  data() {
    return {
      tabview:'',
      showModal: false,
      dataObj:{
      },
      modelTitle: "", 
      dataObj:{
        data:[],
      },
      tableHeader: [
        { name: "序" },
        { name: "数据源" },
        { name: "数据集表名" },
        { name: "数据集说明" },
        { name: "操作" }
      ],
      tableBody: []
    };
  },
  methods: {
    cancel() {},
    cancelDialog(val) {},
    browseData(id){
        this.modelTitle = '数据浏览'
        this.tabview = 'browseData';
        this.showModal = true;
        this.dataObj = {id:id};
    },
  }
};
</script>


